.formulario {
    margin-top: 5rem;

   

    &__contenedor {
        display: flex;
        justify-content: center;
    }
        &__cuerpo {
            width: 82rem;
            height: 45rem;
            background: rgba(255, 255, 255, 0.12);
            border: 3px solid #ffffff;
            box-sizing: border-box;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border-radius: 30px;
        }
            &__enviar {
                width: 9%;
                margin-left: 76.5rem;
                margin-top: -3rem;
                cursor: pointer;
            }
            &__form{
                display: flex;
                justify-content: space-around;
                margin-top: 3rem;
            }

                &__input {
                    width: 32rem;
                    height: 5rem;
                    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
                    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
                    border-radius: 10px;
                    font-family: $letraSecundaria;
                    font-size: 1.5rem;
                }

            &__contMsg{
                display: flex;
                justify-content: center;
                margin-top: 3rem;
            }    
                &__mensaje{
                    width: 73rem;
                    height: 26rem;
                    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
                    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
                    border-radius: 10px;
                    font-family: $letraSecundaria;
                    font-size: 1.5rem;
                }
        &__adorno{
            width: 10%;
            height: 10%;
            margin-top: 38rem;
            margin-left: -10rem;
        }        
}
